import { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import axios from "axios";
import "./OrderDetails.css"; //  
import API_BASE_URL from "../config"; //  API URL
import AddressLink from "./AddressLink";
import FurnitureList from "../components/FurnitureList";


//     OrderDetails

const getSecureFileUrl = (path) => {
  if (!path) return "#";

  if (path.startsWith("http://")) {
    return path.replace("http://", "https://");
  }

  if (path.startsWith("https://")) {
    return path;
  }

  return `${window.location.origin}${path}`;
};

const OrderDetails = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);
  const [downloadedFiles, setDownloadedFiles] = useState(new Set()); //   
  const [selectedImage, setSelectedImage] = useState(null);
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  const [showFurniture, setShowFurniture] = useState(false);
  const [furnitureFile, setFurnitureFile] = useState(null);

  const statusLabels = {
    new: "",
    in_progress: " ",
    waiting: " ",
    agreed: "",
    completed: "",
    cancelled: "",
  };


  const handleEditClick = () => {
    navigate(`/orders/edit/${order.id}`, {
      state: { orderNumber: order.order_number },
    });
  };


  const formatPhone = (num) => {
    if (!num) return "";
  
    //    
    const digits = num.replace(/\D/g, "");
  
    //    11-   +7
    const clean = digits.length === 11 && digits.startsWith("8")
      ? "7" + digits.slice(1)
      : digits;
  
    //   +7 (999) 123-45-67
    return clean.replace(/(\d{1})(\d{3})(\d{3})(\d{2})(\d{2})/, "+$1 ($2) $3-$4-$5");
  };


  useEffect(() => {
    const fetchOrderDetails = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) {
          navigate("/auth");
          return;
        }

        console.log(`    ID: ${id}`);
        const response = await axios.get(`${API_BASE_URL}/api/orders/${id}/`, {
          headers: { Authorization: `Token ${token}` },
        });
        console.log("   :", response.data);
        setOrder(response.data);
      } catch (error) {
        console.error("   :", error.response?.data || error);
        navigate("/dashboard");
      } finally {
        setLoading(false);
      }
    };

    fetchOrderDetails();
  }, [id, navigate]);

  useEffect(() => {
    if (order?.attachments) {
      //     
      const furnitureAttachment = order.attachments.find(file => {
        const fileName = decodeURIComponent(file.file.split("/").pop());
        return fileName.toLowerCase().includes('') && fileName.endsWith('.xls');
      });
      
      if (furnitureAttachment) {
        const fileUrl = getSecureFileUrl(furnitureAttachment.file);

        setFurnitureFile(fileUrl);
      }
    }
  }, [order]);

  if (loading) {
    return <p> ...</p>;
  }

  if (!order) {
    return <p>:   .</p>;
  }

  //       
  const handleDownload = async (fileUrl, fileName) => {
    console.log(`   : ${fileUrl} -> ${fileName}`);
    try {
      const response = await axios.get(fileUrl, { responseType: "blob" });
      console.log("   :", response);
      const blob = new Blob([response.data], { type: response.headers["content-type"] });
      const link = document.createElement("a");
      link.href = window.URL.createObjectURL(blob);
      link.setAttribute("download", fileName);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      console.log("    :", fileName);
      setDownloadedFiles((prev) => new Set(prev).add(fileName));
    } catch (error) {
      console.error("   :", error);
      alert("  .  .");
    }
  };

  //         
  const getFileCategory = (fileName) => {
    const lowerFileName = fileName.toLowerCase();
    
    // :    
    if (lowerFileName.match(/\.(jpg|jpeg|png|gif|bmp|svg|psd|ai|sketch)$/)) {
      return 'sketches';
    }
    
    //   : 3D ,    
    if (lowerFileName.match(/\.(stl|obj|step|stp|dwg|dxf|iges|igs|x_t|sldprt|sldasm|b3d)$/)) {
      return 'production';
    }
    
    //   :  Excel 
    if (lowerFileName.match(/\.(xls|xlsx|xlsm|xlsb|xlt|xltx|xltm)$/)) {
      return 'order';
    }
    
    //  
    return 'other';
  };

  //    
  const groupFilesByCategory = (files) => {
    return files.reduce((acc, file) => {
      const fileName = decodeURIComponent(file.file.split("/").pop());
      const category = getFileCategory(fileName);
      if (!acc[category]) {
        acc[category] = [];
      }
      acc[category].push(file);
      return acc;
    }, {
      sketches: [],
      production: [],
      order: [],
      other: []
    });
  };

  //       
  const handleImageClick = (imageUrl, index) => {
    setSelectedImage(imageUrl);
    setCurrentImageIndex(index);
  };

  //      
  const handleNextImage = (e) => {
    e.stopPropagation();
    const sketches = order.attachments.filter(file => {
      const fileName = decodeURIComponent(file.file.split("/").pop()).toLowerCase();
      return fileName.match(/\.(jpg|jpeg|png|gif|bmp|svg)$/);
    });
    const nextIndex = (currentImageIndex + 1) % sketches.length;
    const nextFile = sketches[nextIndex];
    const nextUrl = nextFile.file.startsWith("http")
      ? nextFile.file
      : `${API_BASE_URL}${nextFile.file}`;
    setSelectedImage(nextUrl);
    setCurrentImageIndex(nextIndex);
  };

  //      
  const handlePrevImage = (e) => {
    e.stopPropagation();
    const sketches = order.attachments.filter(file => {
      const fileName = decodeURIComponent(file.file.split("/").pop()).toLowerCase();
      return fileName.match(/\.(jpg|jpeg|png|gif|bmp|svg)$/);
    });
    const prevIndex = (currentImageIndex - 1 + sketches.length) % sketches.length;
    const prevFile = sketches[prevIndex];
    const prevUrl = prevFile.file.startsWith("http")
      ? prevFile.file
      : `${API_BASE_URL}${prevFile.file}`;
    setSelectedImage(prevUrl);
    setCurrentImageIndex(prevIndex);
  };

  return (
    <div className="order-details-container">
      <h2> {order.order_number}</h2>
      <p>
        <strong>:</strong> {order.full_name || " "}
      </p>
      <p>
  <strong>:</strong>{" "}
  {order.phone_number ? (
    <a
      href={`tel:${order.phone_number}`}
      style={{
        color: "#007bff",
        textDecoration: "none",
        fontWeight: "500",
        display: "inline-flex",
        alignItems: "center",
        gap: "6px",
      }}
    >
       {formatPhone(order.phone_number)}
    </a>
  ) : (
    " "
  )}
</p>
<p>
  <strong>:</strong>{" "}
  {order.address ? <AddressLink address={order.address} /> : " "}
</p>
      <p>
        <strong>:</strong> {order.product || " "}
      </p>
      <p>
        <strong>:</strong> {order.total_price ? `${order.total_price} ` : " "}
      </p>
      <p>
        <strong>:</strong> {order.prepayment ? `${order.prepayment} ` : " "}
      </p>
      <p>
        <strong> :</strong>{" "}
        {order.received_date ? new Date(order.received_date).toLocaleDateString() : " "}
      </p>
      <p>
        <strong> :</strong>{" "}
        {order.due_date ? new Date(order.due_date).toLocaleDateString() : " "}
      </p>
      <p>
        <strong>:</strong> {order.created_by_username || " "}
      </p>
      <p>
  <strong>:</strong> {statusLabels[order.status] || ""}
</p>
      

      {/*      */}
      {selectedImage && (
        <div className="image-modal" onClick={() => setSelectedImage(null)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <button className="close-modal" onClick={() => setSelectedImage(null)}>
              
            </button>
            <button className="modal-nav modal-prev" onClick={handlePrevImage}>
              
            </button>
            <img src={selectedImage} alt="Preview" className="modal-image" />
            <button className="modal-nav modal-next" onClick={handleNextImage}>
              
            </button>
          </div>
        </div>
      )}

      {/*      */}
      {order.attachments && order.attachments.length > 0 && (
        <div className="attachments">
          <strong>:</strong>
          
          {(() => {
            const groupedFiles = groupFilesByCategory(order.attachments);
            return (
              <>
                {/*  */}
                {groupedFiles.sketches.length > 0 && (
                  <div className="attachment-section">
                    <h3>   </h3>
                    <div className="sketches-grid">
                      {groupedFiles.sketches.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        return (
                          <div key={index} className="sketch-item">
                            <img
                              src={fileUrl}
                              alt={fileName}
                              className="sketch-preview"
                              onClick={() => handleImageClick(fileUrl, index)}
                            />
                            <div className="sketch-actions">
                              <button
                                className="sketch-download"
                                style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                                onClick={() => handleDownload(fileUrl, fileName)}
                              >
                                 
                              </button>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                )}

                {/*    */}
                {groupedFiles.production.length > 0 && (
                  <div className="attachment-section">
                    <h3>   </h3>
                    <ul>
                      {groupedFiles.production.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        return (
                          <li key={index}>
                            <button
                              className="download-button"
                              style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                              onClick={() => handleDownload(fileUrl, fileName)}
                            >
                                {fileName}
                            </button>
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                )}

                {/*    */}
                {groupedFiles.order.length > 0 && (
                  <div className="attachment-section">
                    <h3>  </h3>
                    <ul>
                      {groupedFiles.order.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        const isFurnitureFile = fileName.toLowerCase().includes('') && fileName.endsWith('.xls');
                        return (
                          <li key={index} className="file-item">
                            <div className="file-actions">
                              <button
                                className="download-button"
                                style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                                onClick={() => handleDownload(fileUrl, fileName)}
                              >
                                  {fileName}
                              </button>
                              {isFurnitureFile && (
                                <button
                                  className="furniture-button"
                                  onClick={() => setShowFurniture(!showFurniture)}
                                >
                                  {showFurniture ? "  " : "  "}
                                </button>
                              )}
                            </div>
                            {isFurnitureFile && showFurniture && (
                              <div className="furniture-container">
                                <FurnitureList
                                  orderNumber={order.order_number}
                                  fileUrl={fileUrl}
                                />
                              </div>
                            )}
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                )}

                {/*   */}
                {groupedFiles.other.length > 0 && (
                  <div className="attachment-section">
                    <h3>  </h3>
                    <ul>
                      {groupedFiles.other.map((file, index) => {
                        const fileName = decodeURIComponent(file.file.split("/").pop());
                        const fileUrl = getSecureFileUrl(file.file);
                        return (
                          <li key={index}>
                            <button
                              className="download-button"
                              style={{ color: downloadedFiles.has(fileName) ? "gray" : "blue" }}
                              onClick={() => handleDownload(fileUrl, fileName)}
                            >
                                {fileName}
                            </button>
                          </li>
                        );
                      })}
                    </ul>
                  </div>
                )}
              </>
            );
          })()}
        </div>
      )}

      
      {/*    */}
      <div className="button-container">
        <button
          className="back-button"
          onClick={() => navigate("/dashboard")}
        >
          
        </button>
        <button
  className="edit-order-button"
  onClick={handleEditClick}
>
   
</button>
      </div>
    </div>
  );
};

export default OrderDetails;