import { useState, useEffect } from "react";
import { useNavigate, Link } from "react-router-dom";
import axios from "axios";
import API_BASE_URL from "../config";
import "./Settings.css";


const Settings = () => {
  const [companyName, setCompanyName] = useState("");
  const [joinCode, setJoinCode] = useState("");
  const [userInfo, setUserInfo] = useState(null);
  const navigate = useNavigate();

  //       
  useEffect(() => {
    const fetchUserInfo = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) {
          navigate("/auth");
          return;
        }
        const response = await axios.get(`${API_BASE_URL}/api/user_info/`, {
          headers: { Authorization: `Token ${token}` },
        });
        setUserInfo(response.data);
        console.log("   :", response.data);
      } catch (error) {
        console.error("    :", error.response?.data || error);
        navigate("/auth");
      }
    };

    fetchUserInfo();
  }, [navigate]);

  //   
  const copyCodeToClipboard = () => {
    if (userInfo?.organization_code) {
      navigator.clipboard.writeText(userInfo.organization_code);
      alert(" !");
    } else {
      alert("   .");
    }
  };

  //  
const createCompany = async () => {
  try {
    const token = localStorage.getItem("token");
    const response = await axios.post(
      `${API_BASE_URL}/api/create_organization/`,
      { name: companyName },
      { headers: { Authorization: `Token ${token}` } }
    );
    
    alert(` "${companyName}" ! : ${response.data.code}`);

    await fetchUserInfo(); //   
    
    setUserInfo((prev) => ({
      ...prev,
      organization: companyName,
      organization_code: response.data.code,
    }));
    
    console.log("  :", response.data);
  } catch (error) {
    console.error("   :", error.response?.data || error);

    //     ,   
    if (error.response && error.response.data && error.response.data.error) {
      alert(error.response.data.error);
    } else {
      alert("   .  .");
    }
  }
};

  //   
  const joinCompany = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await axios.post(
        `${API_BASE_URL}/api/join_organization/`,
        { code: joinCode },
        { headers: { Authorization: `Token ${token}` } }
      );
      alert(`   : ${response.data.message}`);

      await fetchUserInfo();

      setUserInfo((prev) => ({
        ...prev,
        organization: response.data.message,
      }));
      console.log("   :", response.data);
    } catch (error) {
      console.error("   :", error.response?.data || error);
      alert("  .  .");
    }
  };

  //     :
  // -   "",      delete_organization.
  // -   ,  leave_organization.
  const leaveCompany = async () => {
    if (!window.confirm(" ,    ?")) return;
  
    try {
      const token = localStorage.getItem("token");
      await axios.post(`${API_BASE_URL}/api/leave_organization/`, {}, {
        headers: { Authorization: `Token ${token}` },
      });
  
      alert("  ");
  
      setUserInfo((prev) => ({
        ...prev,
        organization: null,
        organization_code: null,
        role: "individual",
      }));
      navigate("/dashboard");
    } catch (error) {
      alert("    ");
    }
  };

  const deleteOrganization = async () => {
    if (!window.confirm(" ,    ?    .")) return;
  
    try {
      const token = localStorage.getItem("token");
      await axios.post(`${API_BASE_URL}/api/delete_organization/`, {}, {
        headers: { Authorization: `Token ${token}` },
      });
  
      alert(" ");
  
      setUserInfo((prev) => ({
        ...prev,
        organization: null,
        organization_code: null,
        role: "individual",
      }));
      navigate("/dashboard");
    } catch (error) {
      alert("   ");
    }
  };
      

  //   fetchUserInfo :
const fetchUserInfo = async () => {
  try {
    const token = localStorage.getItem("token");
    const response = await axios.get(`${API_BASE_URL}/api/user_info/`, {
      headers: { Authorization: `Token ${token}` },
    });
    setUserInfo(response.data);
    console.log("   :", response.data);
  } catch (error) {
    console.error("    :", error.response?.data || error);
    navigate("/auth");
  }
};
 

  return (
    <div className="settings-container modern-style">
      <h2> </h2>
      <Link to="/settings/contact">  </Link>
      {/*<pre>{JSON.stringify(userInfo, null, 2)}</pre>*/}

      {/*    */}
      {userInfo && (
        <div className="user-info-card">
          <p> {userInfo.username}</p>
          <p>
             {userInfo.organization || " "} | : {userInfo.organization_code || "-"}
          </p>
          {userInfo.organization_code && (
            <button className="copy-button" onClick={copyCodeToClipboard}>
              
            </button>
          )}
        </div>
      )}

      {/*   */}
      {userInfo && userInfo.role === "individual" &&(
      <div className="card-style action-buttons">
        <h3>   </h3>
        <input 
          type="text" 
          placeholder=" " 
          value={companyName} 
          onChange={(e) => setCompanyName(e.target.value)}
          className="full-width-input"
        />
        
        <button className="create-button" onClick={createCompany}> </button>
      </div>)}

      {/*    */}
      {userInfo && userInfo.role === "individual" &&(
      <div className="card-style action-buttons">
        <h3>   </h3>
        <input 
          type="text" 
          placeholder="  " 
          value={joinCode} 
          onChange={(e) => setJoinCode(e.target.value)}
          className="full-width-input"
        />
        <button className="join-button" onClick={joinCompany}> </button>
      </div>)}

      {/*    */}
      {userInfo && userInfo.role === "owner" && (
  <button className="leave-button card-style" onClick={deleteOrganization}>
      
  </button>
)}

{userInfo && userInfo.role === "manager" && (
  <button className="leave-button card-style" onClick={leaveCompany}>
      
  </button>
)}

      {/*     */}
      <button className="dashboard-button card-style" onClick={() => navigate("/dashboard")}>
         
      </button>
    </div>
  );
};



export default Settings;